<template>
    <div class="wrapper" style="overflow: hidden; display: block">
        <div id="thumbnailsGrid" class="col-xs-3 col-sm-6">
            <div class="container" id="gridWrapper">
                <div v-for="(info, pic) in allPhotos"
                     class="thumbnail col-xs-12 col-sm-4"
                     :id="pic" :style="{ 'background-image': 'url(http://localhost:3000/images/' + pic + ')' } "
                     @click="openPic">
                    <input :id="pic" :disabled="$store.state.chosenPhotos.length >= 5" type="checkbox" :value="pic" v-model="$store.state.chosenPhotos">
                </div>
            </div>
        </div>

        <div id="bigPic" class="content col-xs-9 col-sm-6" :style="{ 'background-image': 'url(http://localhost:3000/images/' + openedPic + ')' } ">
            <div id="info-icn">
                <img @click="toggleInfo" @mouseleave="showInfo = false" @mouseover="showInfo = true" src="/src/assets/info.png" style="height: 100%; cursor: pointer; margin-left: 5%">
            </div>
            <transition name="slide-info" mode="out-in">
                <div v-if="showInfo" id="info" >
                    <p style="white-space: nowrap;">{{allPhotos[openedPic]}}</p>
                </div>
            </transition>
        </div>

        <div id="compositeBar" class="col-xs-9 col-sm-6">
            <div class="container-fluid" style="display:flex; height: 60%; width: 90%; margin: auto; padding: 0;">
                <button class="btn btn-info"  @click="uncheckAll" :disabled="$store.state.chosenPhotos.length == 0">CLEAR</button>
                <p style="font-size: large; color: white">{{ $store.state.chosenPhotos.length + '/5' }}</p>
                <button class="btn btn-info" @click="redirectToCompositePage" :disabled="$store.state.chosenPhotos.length < 5">PRINT</button>
            </div>
        </div>
    </div>
</template>

<script>
    import { Media } from '../../../../../store/media';

    export default {
        data() {
            return {
                showInfo: false,
                openedPic: 'classic.jpg',
                allPhotos: Media.book,
                choosePhotos: 5
            }
        },
        methods: {
            toggleInfo() {
                this.showInfo = !this.showInfo;
            },
            openPic() {
                    this.openedPic = event.target.id;
            },
            uncheckAll() {
                this.$store.state.chosenPhotos = [];
            },
            redirectToCompositePage() {
                this.$store.state.levels = 4;
                this.$store.state.clickedEl = 'composite';
                this.$store.state.chainEl = ['home', 'gallery', 'photo', 'composite'];
                this.$router.push('composite');
            }
        }
    }
</script>

<style scoped>
    #thumbnailsGrid {
        height: 100%;
        margin: 0;
        padding: 0;
        overflow: hidden;
    }
    #bigPic {
        height: 90%;
        margin: 0;
        padding: 0;
        background-color: rgba(0, 0, 0, 0.7);
        background-size: auto 100%;
        background-repeat: no-repeat;
        background-position: center;
        overflow: hidden;
    }
    #info {
        position: absolute;
        display: flex;
        align-content: center;
        align-items: center;
        justify-content: center;
        color: white;
        font-size: large;
        font-style: italic;
        font-family: "PT Serif";
        margin: 0;
        padding: 0;
        width: 100%;
        height: 15%;
        border-radius: 5px 5px 0px 0px;
        bottom: 0;
        background-color: rgba(91,192,222,0.5);
        z-index: 1;
    }
    #info-icn {
        position: absolute;
        margin: 0;
        padding: 0;
        width: 100%;
        height: 7%;
        bottom: 0;
        z-index: 2;
    }
    #compositeBar {
        display: flex;
        height: 10%;
        margin: 0;
        padding: 0;
        background-color: rgba(0,0,0,0.7);
        align-items: center;
    }
    #compositeBar button, p {
        display:flex;
        align-items: center;
        justify-content: center;
        margin: 0;
        padding: 3px;
        float:left;
        right: 0px;
        width: 33.33%;
    }

    .thumbnail {
        margin: 0;
        padding: 0;
        height: 30%;
        display: flex;
        float: left;
        cursor: pointer;
        background-color: rgba(0, 0, 0, 0.7);
        background-size: auto 100%;
        background-repeat: no-repeat;
        background-position: center;
        border: none;
        border-radius: 0px;
    }
    #gridWrapper {
        margin: 0;
        padding: 0;
        width: 100%;
        height: 100%;
        overflow-y: scroll;
    }



    .slide-info-enter-active {
        animation: slideInfoIn .5s;
    }
    .slide-info-leave-active {
        animation: slideInfoOut .5s;
    }
    @keyframes slideInfoIn {
        from { transform: translateY(100%); }
        to { transform: translateY(0); }
    }
    @keyframes slideInfoOut {
        from { transform: translateY(0); }
        to { transform: translateY(100%);}
    }


</style>


